import React from 'react';
import ReactDOM from 'react-dom';
import type { StateContainer } from 'state-container';
import Container from './Container';

const containers = new Set<Element>();

// flag
let registered = false;

const registerListener = () => {
  // 避免每次都创建一个 timeout function
  if (registered) return;

  /**
   * why setTimeout:
   * 如果在 angular app run阶段, 渲染react组件, 不能获取到 bodyScope
   * 把事件注册推迟到 angular 渲染完成以后
   * */
  setTimeout(() => {
    // 避免事件监听器多次注册
    if (registered) return;
    const bodyScope = window.angular.element('body').scope();
    const rootScope = bodyScope ? bodyScope.$root : null;

    if (rootScope) {
      // 在路由切换之前卸载react挂载的组件
      rootScope.$on('$stateChangeStart', () => {
        containers.forEach(container => {
          ReactDOM.unmountComponentAtNode(container);
        });
        containers.clear();
      });

      registered = true;
    }
  });
};

export default (
  Component = <></>,
  rootElement: string | Element,
  stateContainer: StateContainer,
  doNotUnmount: boolean = false
) => {
  /**
   * 注册事件监听器, 推迟到第一次render时
   * why: 模块初始化时 rootScope 可能还未创建
   */
  registerListener();

  const container =
    typeof rootElement === 'string' ? document.querySelector(rootElement) : rootElement;

  ReactDOM.render(<Container stateContainer={stateContainer}>{Component}</Container>, container);

  if (!doNotUnmount && container) containers.add(container);
};
