import { BuiltInThemeI } from '../typings/BuiltInTheme';
import fade from './utils/fade';
import { InputDerivationI } from '../typings/DerivationTheme/InputDerivation';
import byColorScheme from './utils/byColorScheme';

export default (theme: BuiltInThemeI) => {
  const createByColorScheme = byColorScheme(theme.colorScheme);

  const inputTheme: InputDerivationI = {
    inputPlaceholderColor: createByColorScheme('#bfbfbf', fade(theme.white, 30)),

    /**
     * 密码查看等 icon hover color
     */
    inputIconColorHover: createByColorScheme(fade(theme.black, 85), fade(theme.white, 85)),
  };

  return { ...inputTheme, ...theme };
};
