import { BgColorDerivationI } from '../typings/DerivationTheme/BgColorDerivation';
import fade from './utils/fade';
import darken from './utils/darken';
import tint from './utils/tint';
import { BuiltInThemeI } from '../typings/BuiltInTheme';
import byColorScheme from './utils/byColorScheme';

const DEFAULT_LIGHT_BG = '#ffffff';
const DEFAULT_DARK_BG = '#282C35';

export default (theme: BuiltInThemeI) => {
  const createByColorScheme = byColorScheme(theme.colorScheme);

  const defaultBg = createByColorScheme(DEFAULT_LIGHT_BG, DEFAULT_DARK_BG);

  const pageBgColor = theme.pageBgColor || defaultBg;

  const bgTheme: BgColorDerivationI = {
    /**
     * 页面背景
     */
    pageBgColor,

    /**
     * 组件背景
     */
    componentBgColor: pageBgColor,

    /**
     * 弹层背景
     */
    popoverBgColor: createByColorScheme(pageBgColor, tint(pageBgColor, 7)),

    /**
     * 某些元素的背景
     * disabled-bg, select-selection-item-bg, progress-remaining-color 等
     * @default colorScheme === light ? darken(white, 4%) : setAlpha(white, 0.08);
     */
    bgColorBase: createByColorScheme(darken(theme.white, 4), fade(theme.white, 8)),

    /**
     * 某些元素的背景
     * input-addon-bg, tag-default-bg, table-footer-bg 等
     * @default colorScheme === light ? darken(white, 2%) : setAlpha(@white, 0.04);
     */
    bgColorLight: createByColorScheme(darken(theme.white, 2), fade(theme.white, 4)),
  };

  return { ...bgTheme, ...theme };
};
