import fade from './utils/fade';
import darken from './utils/darken';
import { BuiltInThemeI } from '../typings/BuiltInTheme';
import byColorScheme from './utils/byColorScheme';
import { ItemBgColorDerivationI } from '../typings/DerivationTheme/ItemBgColorDerivation';

export default (theme: BuiltInThemeI) => {
  const createByColorScheme = byColorScheme(theme.colorScheme);

  const itemActiveBgColor = theme.itemActiveBgColor || theme.primaryColor1;

  const bgTheme: ItemBgColorDerivationI = {
    /**
     * 元素hover背景
     */
    itemHoverBgColor: createByColorScheme(darken(theme.white, 4), fade(theme.white, 8)),
    /**
     * 元素激活(选中)背景
     */
    itemActiveBgColor,
    /**
     * itemActiveBgColor: alpha -> 0.30
     */
    itemActiveBgColorFade30: fade(itemActiveBgColor, 30),
    /**
     * itemActiveBgColor: darken -> 2%
     */
    itemActiveBgColorDarken2: darken(itemActiveBgColor, 2),
  };

  return { ...bgTheme, ...theme };
};
