import { TemplateInfoI } from '@/typings/TemplateInfo';
import request from '@/utils/request';
import { useEffect, useState } from 'react';
import TemplateType from '../enums/TemplateType';

export interface LoadTemplateArgs {
  djcGatewayBaseUrl?: string;
  projectKey?: string;
  templateKey: string;
  cache?: boolean;
  ttl?: number;
}

export interface ComponentInfo {
  name: string;
  props?: object;
}

export const loadTemplate = async ({
  djcGatewayBaseUrl,
  projectKey,
  templateKey,
  cache = true,
  ttl = 60 * 30,
}: LoadTemplateArgs) => {
  if (!djcGatewayBaseUrl) {
    throw new Error('djcGatewayBaseUrl Parameters are missing');
  }

  const portalKey = projectKey || window.top?.location?.host;

  const { data }: { data: TemplateInfoI } = await request(
    `${djcGatewayBaseUrl}/djcsupport/v1/portal-infos/portal-template`,
    {
      params: { portalKey, templateKey },
      cache,
      ttl,
    }
  );

  if (data.templateType === TemplateType.COMPONENT) {
    return {
      name: data.componentName,
      props: data.componentProps ? JSON.parse(data.componentProps) : {},
    };
  }

  throw new Error('模板类型不是组件类型');
};

export const useLoadTemplate = ({
  djcGatewayBaseUrl,
  projectKey,
  templateKey,
  cache,
  ttl,
}: LoadTemplateArgs) => {
  const [componentInfo, setComponentInfo] = useState<ComponentInfo>();

  useEffect(() => {
    loadTemplate({ djcGatewayBaseUrl, projectKey, templateKey, cache, ttl }).then(setComponentInfo);
  }, [cache, djcGatewayBaseUrl, projectKey, templateKey, ttl]);

  return componentInfo;
};
