import { BuiltInThemeI } from '../typings/BuiltInTheme';
import byColorScheme from './utils/byColorScheme';
import { TableDerivationI } from '../typings/DerivationTheme/TableDerivation';

export default (theme: BuiltInThemeI) => {
  const createByColorScheme = byColorScheme(theme.colorScheme);

  const tableTheme: TableDerivationI = {
    tableHeaderSortActiveBg: createByColorScheme('rgba(0, 0, 0, 0.04)', '#393D45'),

    tableHeaderFilterActiveBg: createByColorScheme('rgba(0, 0, 0, 0.04)', '#4a4f57'),
  };

  return { ...tableTheme, ...theme };
};
