import { BorderColorDerivationI } from '../typings/DerivationTheme/BorderColorDerivation';
import { BuiltInThemeI } from '../typings/BuiltInTheme';
import tint from './utils/tint';
import byColorScheme from './utils/byColorScheme';
import darken from './utils/darken';

export default (theme: BuiltInThemeI) => {
  const createByColorScheme = byColorScheme(theme.colorScheme);

  const borderTheme: BorderColorDerivationI = {
    /**
     * 边框色
     */
    borderColorBase: createByColorScheme(
      darken(theme.pageBgColor, 15),
      tint(theme.pageBgColor, 20)
    ),
    /**
     * 组件内部边框
     */
    borderColorSplit: createByColorScheme(
      darken(theme.pageBgColor, 6),
      tint(theme.pageBgColor, 26)
    ),
  };

  return { ...borderTheme, ...theme };
};
