import { generate } from '@ant-design/colors';
import { TinyColor } from '@ctrl/tinycolor';
import { BuiltInThemeI } from '../typings/BuiltInTheme';

export default (theme: BuiltInThemeI) => {
  const linkColor = new TinyColor(theme.linkColor || theme.primaryColor);
  const linkColors = generate(linkColor.toRgbString(), {
    theme: theme.colorScheme === 'dark' ? 'dark' : 'default',
    backgroundColor: theme.componentBgColor,
  });

  const linkTheme = {
    linkColor: linkColor.toRgbString(),
    linkColorHover: linkColors[4],
    linkColorActive: linkColors[6],
  };

  return { ...linkTheme, ...theme };
};
