import { ColorSchemeI } from '@/theme/typings/ColorScheme';
import { originDefaultTheme } from '..';
import derivationCssVars from './derivationCssVars';
import { ConfigServerThemeI, ConfigThemeI } from './typings/ConfigTheme';
import type { ExtraThemeFunctionI, ExtraThemeI } from './typings/Theme';

function caseExtraThemeObject(
  colorScheme: ColorSchemeI,
  configServerTheme?: ConfigServerThemeI,
  extraTheme?: ConfigServerThemeI
) {
  const theme: ConfigThemeI = {
    colorScheme,
    ...originDefaultTheme,
    ...configServerTheme?.base,
    ...configServerTheme?.[colorScheme],
    ...extraTheme?.base,
    ...extraTheme?.[colorScheme],
  };

  return derivationCssVars(theme);
}

function caseExtraThemeFunction(
  colorScheme: ColorSchemeI,
  configServerTheme?: ConfigServerThemeI,
  extraThemeFunction?: ExtraThemeFunctionI
) {
  const config = {
    colorScheme,
    ...originDefaultTheme,
    ...configServerTheme?.base,
    ...configServerTheme?.[colorScheme],
  };

  const extraTheme = extraThemeFunction ? extraThemeFunction(derivationCssVars(config)) : null;

  const theme: ConfigThemeI = {
    ...config,
    ...extraTheme,
  };

  return derivationCssVars(theme);
}

export default (
  /**
   * 当前使用的配色方案
   */
  colorScheme: ColorSchemeI,
  /**
   * 服务器端配置的主题变量
   */
  configServerTheme?: ConfigServerThemeI,
  /**
   * 额外扩展的主题变量
   */
  extraTheme?: ExtraThemeI
) => {
  /**
   * 使用 function 配置, 可通过 colorScheme 与 已配置的主题变量 衍生
   */
  if (typeof extraTheme === 'function') {
    return caseExtraThemeFunction(colorScheme, configServerTheme, extraTheme);
  }

  return caseExtraThemeObject(colorScheme, configServerTheme, extraTheme);
};
