import { generate } from '@ant-design/colors';
import { BuiltInThemeI } from '../typings/BuiltInTheme';

const presetKeys = [
  'red',
  'volcano',
  'orange',
  'gold',
  'yellow',
  'lime',
  'green',
  'cyan',
  'blue',
  'geekblue',
  'purple',
  'magenta',
  'grey',
];

export default (theme: BuiltInThemeI) => {
  const presetColors = {};

  presetKeys.forEach(key => {
    const colors = generate(theme[key], {
      theme: theme.colorScheme === 'dark' ? 'dark' : 'default',
      backgroundColor: theme.componentBgColor,
    });

    colors.forEach((color, index) => {
      presetColors[`${key}${index + 1}`] = color;
    });
  });

  return { ...presetColors, ...theme };
};
