/**
 * 在宿主环境中加载主题配置
 * 或
 * 在子应用中加载宿主环境的
 * 必须放置在 redux <Provider></Provider> 内
 */
import React, { useEffect, useMemo, useState } from 'react';
import { useDispatch } from 'react-redux';
import IEDisableCSSOMInjection from '@/utils/IEDisableCSSOMInjection';
import { ColorSchemeSettingI } from '@/theme/typings/ColorScheme';
import { LoadThemeArgs, useLoadTheme } from './loadTheme';
import setCssVars from './setCssVars';
import { ExtraThemeI } from './typings/Theme';
import makeTheme from './makeTheme';
import useColorScheme from './useColorScheme';

const THEME_SET = '@@THEME/SET';

interface Props extends LoadThemeArgs {
  /**
   * 颜色方案类型
   * @default light
   */
  colorScheme?: ColorSchemeSettingI;
  /**
   * 额外的扩展主题变量
   */
  extraTheme?: ExtraThemeI;
  /**
   * 加载完成后渲染子节点
   */
  renderAfterLoaded?: boolean;
  children: React.ReactNode;
}

export default ({
  colorScheme = 'light',
  extraTheme,
  children,
  djcGatewayBaseUrl,
  projectKey,
  themeKey,
  renderAfterLoaded = true,
  cache,
  ttl,
  dangerouslyForceLoadFromServer,
}: Props) => {
  const dispatch = useDispatch();

  const [loaded, setLoaded] = useState(false);

  const _colorScheme = useColorScheme(colorScheme);

  const configServerTheme = useLoadTheme({
    djcGatewayBaseUrl,
    projectKey,
    themeKey,
    cache,
    ttl,
    dangerouslyForceLoadFromServer,
  });

  const finalTheme = useMemo(
    () => makeTheme(_colorScheme, configServerTheme, extraTheme),
    [_colorScheme, configServerTheme, extraTheme]
  );

  /**
   * 宿主的主题设置存放到 redux 中
   */
  useEffect(() => {
    if (window.top === window.self || dangerouslyForceLoadFromServer) {
      dispatch({
        type: THEME_SET,
        payload: finalTheme,
      });
    }
  }, [dangerouslyForceLoadFromServer, dispatch, finalTheme]);

  useEffect(() => {
    /**
     * 获取数据成功后设置, 避免页面闪烁
     */
    if (configServerTheme) {
      setCssVars(finalTheme);
      setLoaded(true);
    }
  }, [configServerTheme, finalTheme]);

  if (renderAfterLoaded && !loaded) return null;

  return <IEDisableCSSOMInjection>{children}</IEDisableCSSOMInjection>;
};
