import { BuiltInThemeI } from '../typings/BuiltInTheme';
import { LayoutDerivationI } from '../typings/DerivationTheme/LayoutDerivation';
import byColorScheme from './utils/byColorScheme';
import darken from './utils/darken';
import lighten from './utils/lighten';

export default (theme: BuiltInThemeI) => {
  const createByColorScheme = byColorScheme(theme.colorScheme);

  const layoutHeaderBgColor =
    theme.layoutHeaderBgColor || createByColorScheme('#001529', theme.popoverBgColor);

  const layoutTheme: LayoutDerivationI = {
    /**
     * 布局背景色
     */
    layoutBodyBgColor: createByColorScheme('#f0f2f5', theme.pageBgColor),

    /**
     * 顶部布局颜色
     */
    layoutHeaderBgColor,

    /**
     * 菜单收起切换按钮
     */
    layoutTriggerBgColor: createByColorScheme(
      lighten(layoutHeaderBgColor, 5),
      darken(layoutHeaderBgColor, 8)
    ),
  };

  return { ...layoutTheme, ...theme };
};
