import { generate } from '@ant-design/colors';
import { TinyColor } from '@ctrl/tinycolor';
import { BuiltInThemeI } from '../typings/BuiltInTheme';
import fade from './utils/fade';
import lighten from './utils/lighten';
import tint from './utils/tint';

export default (theme: BuiltInThemeI) => {
  const variables: Record<string, string> = {};

  const primaryColor = new TinyColor(theme.originPrimaryColor);
  const primaryColors = generate(primaryColor.toRgbString(), {
    theme: theme.colorScheme === 'dark' ? 'dark' : 'default',
    backgroundColor: theme.componentBgColor,
  });

  // eslint-disable-next-line prefer-destructuring
  variables.primaryColorHover = primaryColors[4];
  // eslint-disable-next-line prefer-destructuring
  variables.primaryColorActive = primaryColors[6];

  variables.primaryColorOutline = fade(primaryColor, 20);

  primaryColors.forEach((color, index) => {
    /**
     * 强制覆盖 primaryColor
     * 色板算法 dark 模式下, primaryColors[5] !== primaryColor
     */
    if (index === 5) {
      variables.primaryColor = color;
    } else {
      variables[`primaryColor${index + 1}`] = color;
    }
  });

  variables.primaryColorLighten35 = lighten(primaryColor, 35);
  variables.primaryColorLighten20 = lighten(primaryColor, 20);
  variables.primaryColorTint20 = tint(primaryColor, 20);
  variables.primaryColorTint50 = tint(primaryColor, 50);
  variables.primaryColorFade12 = fade(primaryColor, 12);
  variables.primaryColorFade20 = fade(primaryColor, 20);

  return { ...variables, ...theme };
};
