export default (v1: any, v2: any) => {
  if ((v1 === undefined && v2 === undefined) || (v1 === null && v2 === null)) return true;

  let _v1 = v1;
  if (typeof v1 === 'object') {
    try {
      _v1 = JSON.stringify(v1);
    } catch {
      // nothing
    }
  }
  let _v2 = v2;
  if (typeof v2 === 'object') {
    try {
      _v2 = JSON.stringify(v2);
    } catch {
      // nothing
    }
  }

  return _v1 === _v2;
};
