import request from '@/utils/request';
import { useEffect, useState } from 'react';
import useTheme from './useTheme';
import { ConfigServerThemeI } from './typings/ConfigTheme';

/**
 * 仅在宿主环境有效
 */
export interface LoadThemeArgs {
  /**
   * 网关 baseUrl
   */
  djcGatewayBaseUrl: string;
  /**
   * 项目 key
   */
  projectKey?: string;
  /**
   * 主题配置方案 key
   */
  themeKey?: string;
  /**
   * 是否缓存
   */
  cache?: boolean;
  /**
   * 缓存有效期
   * @default 1800s
   */
  ttl?: number;
  /**
   * (子应用[iframe])是否强制从服务器端加载主题
   * 默认为从顶层窗口获取主题变量设置, 保持UI一致性
   * *** 注意: 只能在顶层窗口无主题设置时使用, 否则可能会引起混乱 ***
   * @default false
   */
  dangerouslyForceLoadFromServer?: boolean;
}

export const loadTheme = async ({
  djcGatewayBaseUrl,
  projectKey,
  themeKey = 'default',
  cache = true,
  ttl = 60 * 30,
}: LoadThemeArgs) => {
  try {
    const portalKey = projectKey || window.top?.location?.host;

    const { data } = await request(`${djcGatewayBaseUrl}/djcsupport/v1/portal-infos/portal-theme`, {
      params: { portalKey, themeKey },
      cache,
      ttl,
    });

    const themeConfig = data && data.themeSettings ? JSON.parse(data.themeSettings) : {};

    return themeConfig;
  } catch {
    return {};
  }
};

export const useLoadTheme = ({
  djcGatewayBaseUrl,
  projectKey,
  themeKey,
  cache,
  ttl,
  dangerouslyForceLoadFromServer,
}: LoadThemeArgs) => {
  const [theme, setTheme] = useState<ConfigServerThemeI>();

  /**
   * 宿主环境从服务器端加载配置
   */
  useEffect(() => {
    if (window.top === window.self || dangerouslyForceLoadFromServer) {
      if (djcGatewayBaseUrl)
        loadTheme({ djcGatewayBaseUrl, projectKey, themeKey, cache, ttl }).then(setTheme);
      else {
        setTheme({});
      }
    }
  }, [cache, dangerouslyForceLoadFromServer, djcGatewayBaseUrl, projectKey, themeKey, ttl]);

  /**
   * 子应用从 redux 中获取配置
   */
  const parentTheme = useTheme();

  if (window.top !== window.self && !dangerouslyForceLoadFromServer) {
    // 构造为和配置服务器返回相同的结构
    return { base: parentTheme };
  }

  return theme;
};
