import isMSIE from '@/utils/isMSIE';
import omitBy from '@/utils/omitBy';
import cssVars from 'css-vars-ponyfill';
import { ThemeI } from './typings/Theme';

/**
 * 保存上一次的变量, 用于找出需要清理的变量
 */
let prevVars;

/**
 * 找出在新的 vars 中不存在的 prevVars key
 * 这部分变量需要清除, 避免干扰
 */
const findNeedRemoveKeys = (vars: object, pVars?: object) => {
  if (!pVars) return [];

  const removeKeys: Array<string> = [];

  const pKeys = Object.keys(pVars);
  const Keys = Object.keys(vars);

  pKeys.forEach(key => {
    if (!Keys.includes(key)) {
      removeKeys.push(key);
    }
  });

  return removeKeys;
};

/**
 * 需要删除的key, 值设置为 null
 */
const createRemoveVars = (removeKeys: Array<string>) => {
  const o = {};
  removeKeys.forEach(key => {
    o[key] = null;
  });
  return o;
};

export default (vars: ThemeI) => {
  /**
   * 排除值类型无效的变量
   */
  const validVars = omitBy(
    vars,
    v => typeof v !== 'string' && typeof v !== 'number' && typeof v !== 'boolean'
  ) as ThemeI;

  const removeKeys = findNeedRemoveKeys(validVars, prevVars);
  const removeVars = createRemoveVars(removeKeys);

  cssVars({
    variables: { ...validVars, ...removeVars },
    watch: isMSIE(),
  });

  prevVars = validVars;

  /**
   * https://developer.mozilla.org/zh-CN/docs/Web/CSS/color-scheme
   * color-scheme CSS 属性允许元素指示它可以轻松呈现的配色方案。
   * 例如: 滚动条
   */
  if (validVars && validVars.colorScheme) {
    document.documentElement.style.colorScheme = validVars.colorScheme;
  }
};
