import { RadioDerivationI } from '../typings/DerivationTheme/RadioDerivation';
import { BuiltInThemeI } from '../typings/BuiltInTheme';
import fade from './utils/fade';
import byColorScheme from './utils/byColorScheme';

export default (theme: BuiltInThemeI) => {
  const createByColorScheme = byColorScheme(theme.colorScheme);

  const radioTheme: RadioDerivationI = {
    radioSolidCheckedColor: createByColorScheme(theme.componentBgColor, theme.white),

    radioDotDisabledColor: createByColorScheme(fade(theme.black, 20), fade(theme.white, 20)),

    radioDisabledButtonCheckedBgColor: createByColorScheme(
      theme.disabledActiveBgColor,
      fade(theme.white, 20)
    ),
  };

  return { ...radioTheme, ...theme };
};
