import { useCallback, useEffect, useState } from 'react';
import { ColorSchemeSettingI } from './typings/ColorScheme';

const PREFERS_COLOR_SCHEME_DARK = '(prefers-color-scheme: dark)';

let mediaRef: MediaQueryList;

// get system setting
export const getPrefersColorMedia = () => {
  if (mediaRef) return mediaRef;
  try {
    mediaRef = window.matchMedia(PREFERS_COLOR_SCHEME_DARK);
    return mediaRef;
  } catch {
    return null;
  }
};

export const getColorScheme = (cst: ColorSchemeSettingI) => {
  if (cst === 'syncWithSystem') {
    const media = getPrefersColorMedia();
    return media && media.matches ? 'dark' : 'light';
  }
  return cst;
};

export default (cst: ColorSchemeSettingI) => {
  // init
  const [colorScheme, setColorScheme] = useState(() => getColorScheme(cst));

  // watch cst change
  useEffect(() => {
    setColorScheme(getColorScheme(cst));
  }, [cst]);

  const callback = useCallback((e: MediaQueryListEvent) => {
    setColorScheme(e.matches ? 'dark' : 'light');
  }, []);

  // watch system setting change
  useEffect(() => {
    if (cst === 'syncWithSystem') {
      const media = getPrefersColorMedia();

      if (media) {
        media.addEventListener('change', callback);
        return () => media.removeEventListener('change', callback);
      }
    }
  }, [callback, cst]);

  return colorScheme;
};
