import { DisabledColorDerivationI } from '../typings/DerivationTheme/DisabledColorDerivation';
import { BuiltInThemeI } from '../typings/BuiltInTheme';
import fade from './utils/fade';
import tint from './utils/tint';
import byColorScheme from './utils/byColorScheme';

export default (theme: BuiltInThemeI) => {
  const createByColorScheme = byColorScheme(theme.colorScheme);

  const disabledColor = createByColorScheme(fade(theme.black, 35), fade(theme.white, 30));

  const disabledTheme: DisabledColorDerivationI = {
    disabledColor,

    disabledColorDark: createByColorScheme(fade(theme.white, 45), fade(theme.white, 30)),

    disabledActiveColor: createByColorScheme(disabledColor, theme.black),

    disabledActiveBgColor: createByColorScheme(tint(theme.black, 90), fade(theme.white, 25)),
  };

  return { ...disabledTheme, ...theme };
};
