import { SkeletonDerivationI } from '../typings/DerivationTheme/SkeletonDerivation';
import { BuiltInThemeI } from '../typings/BuiltInTheme';
import byColorScheme from './utils/byColorScheme';
import shade from './utils/shade';
import fade from './utils/fade';

export default (theme: BuiltInThemeI) => {
  const createByColorScheme = byColorScheme(theme.colorScheme);

  const skeletonTheme: SkeletonDerivationI = {
    /**
     * 骨架渐变色
     */
    skeletonToColor: createByColorScheme(shade(theme.skeletonColor, 5), fade(theme.white, 16)),
  };

  return { ...skeletonTheme, ...theme };
};
