import React, { createContext } from 'react';
import { TemplateProps } from './Template';

export interface TemplateConfigProviderProps extends Omit<TemplateProps, 'templateKey'> {
  children?: React.ReactNode;
}

export const TemplateConfigContext = createContext<TemplateConfigProviderProps>({});

const TemplateConfigProvider = ({ children, ...restProps }: TemplateConfigProviderProps) => (
  <TemplateConfigContext.Provider value={restProps}>{children}</TemplateConfigContext.Provider>
);

export default TemplateConfigProvider;
