import md5 from 'md5';

const storage = sessionStorage;

/**
 * 获取本地缓存
 * @param key 缓存key
 * @param ttl 缓存时长
 * @returns
 */
export const getCache = (key: string) => {
  const _key = md5(key);
  const data = storage.getItem(_key);
  if (data) {
    const _data = JSON.parse(data);
    /**
     * 判断缓存是否失效
     */
    if (_data.timestamp + _data.ttl * 1000 > Date.now()) return _data.value;
    /**
     * 删除失效缓存
     */
    storage.removeItem(_key);
  }
  return null;
};

/**
 * 设置本地缓存
 * @param key 缓存key
 * @param value 缓存内容
 * @param ttl 缓存时长
 * @returns
 */
export const setCache = (key: string, value: any, ttl: number) => {
  const _key = md5(key);
  const _value = JSON.stringify({
    timestamp: Date.now(),
    ttl,
    value,
  });

  storage.setItem(_key, _value);
};

export const removeCache = (key: string) => {
  const _key = md5(key);
  storage.removeItem(_key);
};
