/**
 * 在宿主环境中加载主题配置
 */
import type { Dispatch } from 'redux';
import { loadTheme, LoadThemeArgs } from './loadTheme';
import setCssVars from './setCssVars';
import makeTheme from './makeTheme';
import { ExtraThemeI, ThemeI } from './typings/Theme';
import { ColorSchemeI, ColorSchemeSettingI } from './typings/ColorScheme';
import { getColorScheme, getPrefersColorMedia } from './useColorScheme';

const THEME_SET = '@@THEME/SET';

interface Args extends LoadThemeArgs {
  colorScheme?: ColorSchemeSettingI;
  extraTheme?: ExtraThemeI;
  dispatch: Dispatch;
}

function setTheme(theme: ThemeI, dispatch: Dispatch<any>) {
  setCssVars(theme);

  dispatch({
    type: THEME_SET,
    payload: theme,
  });
}

export default async ({
  colorScheme = 'light',
  extraTheme,
  djcGatewayBaseUrl,
  projectKey,
  themeKey,
  dispatch,
  cache,
  ttl,
  dangerouslyForceLoadFromServer,
}: Args) => {
  // 加载服务器端配置
  const configServerTheme = await loadTheme({
    djcGatewayBaseUrl,
    projectKey,
    themeKey,
    cache,
    ttl,
    dangerouslyForceLoadFromServer,
  });

  // 应用主题
  function applyTheme(cs: ColorSchemeI) {
    const finalTheme = makeTheme(cs, configServerTheme, extraTheme);
    setTheme(finalTheme, dispatch);
  }

  // 颜色模式跟随系统设置时的响应回调
  const callback = (e: MediaQueryListEvent) => {
    const cs = e.matches ? 'dark' : 'light';
    applyTheme(cs);
  };

  // 切换颜色模式
  function switchColorScheme(css: ColorSchemeSettingI) {
    const cs = getColorScheme(css);

    applyTheme(cs);

    const media = getPrefersColorMedia();

    if (css === 'syncWithSystem') {
      if (media) {
        media.addEventListener('change', callback);
      }
    } else if (media) {
      media.removeEventListener('change', callback);
    }
  }

  // init
  switchColorScheme(colorScheme);

  return switchColorScheme;
};
