import { BuiltInThemeI } from '../typings/BuiltInTheme';
import fillColor from './utils/fillColor';

export default (theme: BuiltInThemeI) => {
  const successTheme = fillColor(
    theme.originSuccessColor,
    'success',
    theme.colorScheme,
    theme.componentBgColor
  );

  return { ...successTheme, ...theme };
};
