import { BuiltInThemeI } from '../typings/BuiltInTheme';
import byColorScheme from './utils/byColorScheme';
import { ShadowDerivationI } from '../typings/DerivationTheme/ShadowDerivation';

export default (theme: BuiltInThemeI) => {
  const createByColorScheme = byColorScheme(theme.colorScheme);

  const shadowTheme: ShadowDerivationI = {
    shadowColor: createByColorScheme('rgba(0, 0, 0, 0.15)', 'rgba(0, 0, 0, 0.45)'),

    boxShadowBase: createByColorScheme(
      `0 3px 6px -4px rgba(0, 0, 0, 0.15),
      0 6px 16px 0 rgba(0, 0, 0, 0.08)`,
      `0 3px 6px -4px rgba(0, 0, 0, 0.45), 
      0 6px 16px 0 rgba(0, 0, 0, 0.32)`
    ),

    shadow1Up: createByColorScheme(
      `0 -6px 16px -8px rgba(0, 0, 0, 0.08),
      0 -9px 28px 0 rgba(0, 0, 0, 0.05)`,
      `0 -6px 16px -8px rgba(0, 0, 0, 0.32),
      0 -9px 28px 0 rgba(0, 0, 0, 0.2)`
    ),

    shadow1Down: createByColorScheme(
      `0 6px 16px -8px rgba(0, 0, 0, 0.08), 
      0 9px 28px 0 rgba(0, 0, 0, 0.05)`,
      `0 6px 16px -8px rgba(0, 0, 0, 0.32),
      0 9px 28px 0 rgba(0, 0, 0, 0.2)`
    ),

    shadow1Left: createByColorScheme(
      `-6px 0 16px -8px rgba(0, 0, 0, 0.08),
      -9px 0 28px 0 rgba(0, 0, 0, 0.05)`,
      `-6px 0 16px -8px rgba(0, 0, 0, 0.32),
      -9px 0 28px 0 rgba(0, 0, 0, 0.2)`
    ),

    shadow1Right: createByColorScheme(
      `6px 0 16px -8px rgba(0, 0, 0, 0.08),
      9px 0 28px 0 rgba(0, 0, 0, 0.05)`,
      `6px 0 16px -8px rgba(0, 0, 0, 0.32), 
      9px 0 28px 0 rgba(0, 0, 0, 0.2)`
    ),

    waveShadowColor: theme.primaryColor,
  };

  return { ...shadowTheme, ...theme };
};
