import { generate } from '@ant-design/colors';
import { TinyColor } from '@ctrl/tinycolor';
import { BuiltInThemeI } from '../typings/BuiltInTheme';

export default (theme: BuiltInThemeI) => {
  const variables: Record<string, string> = {};

  const secondaryColor = new TinyColor(theme.originSecondaryColor);
  const secondaryColors = generate(secondaryColor.toRgbString(), {
    theme: theme.colorScheme === 'dark' ? 'dark' : 'default',
    backgroundColor: theme.componentBgColor,
  });

  // eslint-disable-next-line prefer-destructuring
  variables.secondaryColorHover = secondaryColors[4];
  // eslint-disable-next-line prefer-destructuring
  variables.secondaryColorActive = secondaryColors[6];

  secondaryColors.forEach((color, index) => {
    /**
     * 强制覆盖 secondaryColor
     * 色板算法 dark 模式下, secondaryColors[5] !== secondaryColor
     */
    if (index === 5) {
      variables.secondaryColor = color;
    } else {
      variables[`secondaryColor${index + 1}`] = color;
    }
  });

  return { ...variables, ...theme };
};
