import { TextColorDerivationI } from '../typings/DerivationTheme/TextColorDerivation';
import { BuiltInThemeI } from '../typings/BuiltInTheme';
import fade from './utils/fade';

export default (theme: BuiltInThemeI): TextColorDerivationI => {
  const x = theme.colorScheme === 'light' ? 'black' : 'white';

  const textTheme = {
    textColor: fade(theme[x], 85),
    textSecondaryColor: fade(theme[x], 55),
    iconColorHover: fade(theme[x], 75),
  };

  return { ...textTheme, ...theme };
};
