import { ColorSchemeI } from '@/theme/typings/ColorScheme';
import { generate } from '@ant-design/colors';
import { TinyColor } from '@ctrl/tinycolor';

export default (colorVal: string, type: string, colorScheme: ColorSchemeI, bgColor: string) => {
  const baseColor = new TinyColor(colorVal);
  const colorPalettes = generate(baseColor.toRgbString(), {
    theme: colorScheme === 'dark' ? 'dark' : 'default',
    backgroundColor: bgColor,
  });

  return {
    [`${type}Color`]: colorPalettes[5],
    [`${type}ColorDisabled`]: colorPalettes[0],
    [`${type}ColorHover`]: colorPalettes[4],
    [`${type}ColorActive`]: colorPalettes[6],
    [`${type}ColorOutline`]: baseColor.clone().setAlpha(0.2).toRgbString(),
    [`${type}ColorBg`]: colorPalettes[0],
    [`${type}ColorBorder`]: colorPalettes[2],
  };
};
