import React from 'react';
import type { StateContainer } from 'state-container';
import ReactDOM from 'react-dom';
import isEqual from '@/utils/isEqual';
import reactRender from './reactRender';
import Template, { TemplateProps } from './Template';

export interface Args extends Omit<TemplateProps, 'templateKey'> {
  stateContainer: StateContainer;
}

function templateDirective(args: Args) {
  window.angular.module('gmsoft.template', []).directive('template', () => ({
    restrict: 'EA',
    template: '<div></div>',
    scope: {
      fallback: '=?',
      cloudComponent: '=?',
      stateContainer: '=?',
      djcGatewayBaseUrl: '@?',
      projectKey: '@?',
      templateKey: '@',
      extraProps: '=?',
    },
    replace: true,
    link(scope: any, element) {
      const el = element[0];

      const render = () => {
        const {
          fallback,
          cloudComponent,
          stateContainer,
          djcGatewayBaseUrl,
          projectKey,
          templateKey,
          extraProps,
        }: any = { ...args, ...scope };

        /**
         * scope.djcGatewayBaseUrl 和 scope.projectKey
         * 的值可能为 undefined
         */
        const _djcUrl = djcGatewayBaseUrl || args.djcGatewayBaseUrl;
        const _projectKey = projectKey || args.projectKey;

        reactRender(
          <Template
            cloudComponent={cloudComponent}
            fallback={fallback}
            djcGatewayBaseUrl={_djcUrl}
            projectKey={_projectKey}
            templateKey={templateKey}
            extraProps={extraProps}
          />,
          el,
          stateContainer,
          true
        );
      };

      /**
       * 渲染组件
       */
      render();

      /**
       * 检测 props 变化, 重新渲染
       */
      ['djcGatewayBaseUrl', 'projectKey', 'templateKey', 'extraProps'].forEach(x =>
        scope.$watchCollection(x, (newValue?: any, oldValue?: any) => {
          if (!isEqual(oldValue, newValue)) render();
        })
      );

      /**
       * 销毁组件
       */
      scope.$on('$destroy', () => {
        ReactDOM.unmountComponentAtNode(el);
      });
    },
  }));

  return 'gmsoft.template';
}

export default templateDirective;
