import { TinyColor } from '@ctrl/tinycolor';
import { BuiltInThemeI } from '../typings/BuiltInTheme';
import darken from './utils/darken';
import fade from './utils/fade';

export default (theme: BuiltInThemeI) => {
  const variables: Record<string, string> = {};

  const white = new TinyColor(theme.white);

  [1, 65, 85].forEach(amount => {
    variables[`whiteFade${amount}`] = fade(white, amount);
  });

  [4, 8, 12, 16, 20, 25, 30, 45, 75].forEach(amount => {
    variables[`whiteDarken${amount}`] = darken(white, amount);
  });

  return { ...variables, ...theme };
};
