import { ButtonDerivationI } from '../typings/DerivationTheme/ButtonDerivation';
import { BuiltInThemeI } from '../typings/BuiltInTheme';
import fade from './utils/fade';
import byColorScheme from './utils/byColorScheme';

export default (theme: BuiltInThemeI) => {
  const createByColorScheme = byColorScheme(theme.colorScheme);

  const buttonTheme: ButtonDerivationI = {
    /**
     * 幽灵按钮文字颜色
     */
    btnDefaultGhostColor: createByColorScheme(theme.componentBgColor, theme.textColor),

    /**
     * 幽灵边框颜色
     */
    btnDefaultGhostBorder: createByColorScheme(theme.componentBgColor, fade(theme.white, 25)),

    /**
     * antd4 type=text
     */
    btnTextHoverBg: createByColorScheme('rgba(0, 0, 0, 0.018)', 'rgba(255, 255, 255, 0.03)'),
  };

  return { ...buttonTheme, ...theme };
};
