import { SuspenseProps, CloudComponentI } from '@/typings/CloudComponent';
import React, { useContext, useMemo } from 'react';
import { IEDisableCSSOMInjection } from '..';
import { LoadTemplateArgs, useLoadTemplate } from './loadTemplate';
import { TemplateConfigContext } from './TemplateConfigProvider';

export interface TemplateProps extends LoadTemplateArgs, SuspenseProps {
  cloudComponent?: CloudComponentI;
  extraProps?: object;
}

export default (props: TemplateProps) => {
  const context = useContext(TemplateConfigContext);

  const mergedProps = useMemo(() => ({ ...context, ...props }), [context, props]);

  const { cloudComponent: CloudComponent, extraProps, ...restProps } = mergedProps;

  const componentInfo = useLoadTemplate(restProps);

  if (!CloudComponent) {
    // eslint-disable-next-line no-console
    console.error('Template need props CloudComponent');
  }

  if (componentInfo?.name && CloudComponent) {
    return (
      <IEDisableCSSOMInjection>
        <CloudComponent
          name={componentInfo.name}
          {...componentInfo.props}
          {...extraProps}
          fallback={restProps?.fallback}
        />
      </IEDisableCSSOMInjection>
    );
  }

  if (restProps?.fallback)
    return <IEDisableCSSOMInjection>{restProps?.fallback}</IEDisableCSSOMInjection>;

  return null;
};
