import axios, { AxiosRequestConfig } from 'axios';
import qs from 'qs';
import { getCache, removeCache, setCache } from './localCache';

interface Cache {
  /**
   * 是否启用本地缓存
   */
  cache?: boolean;
  /**
   * 单位秒
   */
  ttl?: number;
}

function createCacheKey(url: string, config?: AxiosRequestConfig) {
  return `${url}_${JSON.stringify(config?.params || {})}`;
}

function cacheIsEnabled(config?: AxiosRequestConfig & Cache) {
  return (
    (config?.method === 'GET' || !config?.method) && config?.cache && config?.ttl && config?.ttl > 0
  );
}

const request = (url: string, config?: AxiosRequestConfig & Cache) => {
  /**
   * 读取缓存
   */
  if (cacheIsEnabled(config)) {
    const data = getCache(createCacheKey(url, config));
    if (data) return Promise.resolve({ data });
  } else {
    /**
     * 删除可能存在的缓存
     */
    removeCache(createCacheKey(url, config));
  }

  return axios({
    url,
    withCredentials: true,
    paramsSerializer(params) {
      return qs.stringify(params, { arrayFormat: 'repeat', allowDots: true });
    },
    ...config,
  }).then(res => {
    /**
     * 写入缓存
     */
    if (cacheIsEnabled(config)) {
      setCache(createCacheKey(url, config), res.data, config?.ttl!);
    }
    return res;
  });
};

export default request;
